
import React, {Fragment} from 'react';
 


const Form = (props) => {

 


  return (
		<div className="container">
			<form className="col s12" >
				<div className="row">
					<div className="input-field col s6">
						<i className="material-icons prefix">web_asset</i>
						<input id="ciudad" name="ciudad" type="text" className="validate"/>
						<label htmlFor="icon_prefix">Ciudad</label>
					</div>
					<div className="input-field col s6">
						<i className="material-icons prefix">terrain</i>
						<input id="pais" name="pais" type="text" className="validate"/>
						<label htmlFor="icon_telephone">Pais</label>
					</div>
					<div className="input-field col s6">
						
						<button className="btn waves-effect waves-light btn-small" type="submit" name="action">Submit
						<i className="material-icons right">send</i>
						</button>
					</div>
				</div>
			</form>
		</div>
	);
  }

  export default Form;